<template>
  <div class="member-box">
    <div class="query-wrapper">
      <div>
        <el-form :inline="true" :model="queryForm" ref="queryForm" label-width="100px">
          <el-form-item label="我的简报">
            <el-select class="w100" v-model="queryForm.type" :placeholder="'select' | solvePlaceholder">
              <el-option
                v-for="item in briefs"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </div>
      <div class="query-btn-group mt15">
        <el-button type="primary" @click="queryContent">查询</el-button>
      </div>
    </div>
    <el-row  type="flex" justify="space-around">
      <el-col :span="11" class="customer-list">
        <div class="add-customer customer-common">
          <div class="num">{{newIncreCount}}</div>
          <div class="title">新增客户数</div>
        </div>
        <div class="contact-customer customer-common">
          <div class="num">{{flowerCount}}</div>
          <div class="title">联系跟进次数</div>
        </div>
      </el-col>
      <el-col :span="11"/>
    </el-row>
    <el-row type="flex" justify="space-around">
      <el-col :span="11">
        <div class="ranks">
          <div class="head">
            <div class="title">排行榜</div>
            <div class="head-query">
              <el-form inline >
                <el-form-item prop="ranking">
                  <el-select clearable class="w100" v-model="rankForm.limit" :placeholder="'select' | solvePlaceholder">
                    <el-option label="前五名" value="5"/>
                    <el-option label="前十名" value="10"/>
                  </el-select>
                </el-form-item>
                <el-form-item prop="range">
                  <el-select clearable class="w100" v-model="rankForm.deptId" :placeholder="'select' | solvePlaceholder">
                    <el-option
                      v-for="item in trees"
                      :key="item | solveKey"
                      :label="item.deptName"
                      :value="item.id">
                    </el-option>
                  </el-select>
                </el-form-item>
                <el-form-item prop="myBriefing">
                  <el-select clearable class="w100" v-model="rankForm.type" :placeholder="'select' | solvePlaceholder">
                    <el-option
                      v-for="item in briefs"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                    </el-option>
                  </el-select>
                </el-form-item>
                <el-form-item>
                  <el-button type="primary" @click="queryRanks">查询</el-button>
                </el-form-item>
              </el-form>
            </div>
          </div>
          <div class="rank-list">
            <el-table :data="rankList">
              <el-table-column type="index" width="50"/>
              <el-table-column prop="name" label="姓名" />
              <el-table-column label="进度" >
                <template slot-scope="scope">
                  <el-progress :text-inside="true" :stroke-width="26" :percentage="scope.row.percentage"></el-progress>
                </template>
              </el-table-column>
              <el-table-column prop="customerNum" label="数量" />
            </el-table>
          </div>
        </div>
      </el-col>
      <el-col :span="11">
        <div class="wait-order">
          <div class="head">
            <div class="title">待办工单</div>
          </div>
          <div class="rank-list">
            <el-table :data="waitOrders">
              <el-table-column prop="id" label="工单号" />
              <el-table-column label="工单时间" show-overflow-tooltip>
                <template slot-scope="scope">{{scope.row.gmtCreateTime | solveTime}}</template>
              </el-table-column>
              <el-table-column label="工单类型" >
                <template slot-scope="scope">{{scope.row.type==0?'跟进客户':''}}</template>
              </el-table-column>
              <el-table-column prop="task" label="工单内容" show-overflow-tooltip />
              <el-table-column label="操作 " >
                <template slot-scope="scope" v-if="scope.row.status == 0">
                  <el-button type="primary" size="mini" @click="handleTask(scope.row)" >处理</el-button>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </div>
      </el-col>
    </el-row>
    <el-row type="flex" justify="space-around" class="mt15">
      <el-col :span="11">
        <div class="assign-order">
          <div class="head">
            <div class="title">指派工单</div>
          </div>
          <div class="rank-list">
            <el-table :data="assignOrders">
              <el-table-column prop="staffName" label="员工名称" />
              <el-table-column prop="id" label="工单号" />
              <el-table-column label="工单时间" show-overflow-tooltip>
                <template slot-scope="scope">{{scope.row.gmtCreateTime | solveTime}}</template>
              </el-table-column>
              <el-table-column label="工单类型" >
                <template slot-scope="scope">{{scope.row.type==0?'跟进客户':''}}</template>
              </el-table-column>
              <el-table-column prop="task" label="工单内容" show-overflow-tooltip />
              <el-table-column  label="处理结果" >
                <template slot-scope="scope">{{scope.row.status | solveResult}}</template>
              </el-table-column>
            </el-table>
          </div>
        </div>
      </el-col>
      <el-col :span="11"></el-col>
    </el-row>

    <el-dialog title="工单处理" :visible.sync="dialogHandleTaskVisible">
      <el-form :model="handleTaskForm" ref="handleTaskForm">
        <el-form-item label="工单号" prop="id">
          <el-input readonly v-model="handleTaskForm.id" :placeholder="'input' | solvePlaceholder" />
        </el-form-item>

        <el-form-item label="处理结果" prop="status">
          <el-select v-model="handleTaskForm.status" :placeholder="'select' | solvePlaceholder"  class="w100">
            <el-option value="0" label="待处理"></el-option>
            <el-option value="1" label="处理完成"></el-option>
            <el-option value="2" label="无法处理"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="备注" prop="remark">
          <el-input type="textarea" :row="2" v-model="handleTaskForm.remark" :placeholder="'input' | solvePlaceholder" />
        </el-form-item>
      </el-form>

      <span slot="footer">
        <el-button type="primary" @click="saveTask('handleTaskForm')">保存</el-button>
        <el-button @click="cancelTask('handleTaskForm')">取消</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
  import {Apis} from '@/axios'
  import {Message} from 'element-ui'
  import {treeToArray, removeDuplicateObj} from '@/utils/tools'

  export default {
    name: 'Dashboard',
    data() {
      return {
        queryForm: {
          type:'-1'
        },
        briefs:[ { label:'全部', value:'-1' },{ label:'昨日', value:'0' },{ label:'今日', value:'1' },{ label:'上周', value:'2' },{ label:'本周', value:'3' },{ label:'上月', value:'4' },{ label:'本月', value:'5' }],
        flowerCount: null,
        newIncreCount: null,
        rankForm: {
          limit: '5',
          type: '3',
          deptId:'1'
        },
        rankList:[],
        waitOrders:[],
        trees:[],
        assignOrders:[],
        dialogHandleTaskVisible: false,
        handleTaskForm: {
          id:'',
          status:'',
          remark: ''
        }
      }
    },
    mounted() {
      let _this = this
      _this.init()
    },
    methods: {
      init() {
        let _this = this
        _this.queryContent()
        _this.departTree()
        _this.getMyTask()
        _this.getAssignTask()
      },
      queryContent() {
        let _this = this
        let {getMyReport} = Apis.member
        getMyReport(_this.queryForm).then(res => {
          if (res.data.status == 200) {
            let {flowerCount, newIncreCount} = res.data.data
            _this.flowerCount = flowerCount
            _this.newIncreCount = newIncreCount
          } else Message.error(res.data.msg)
        })
      },
      departTree(){
        let _this = this
        let { deptTree } = Apis.member
        deptTree().then(res => {
          if (res.data.status == 200) {
            let trees = treeToArray(res.data.data)
            _this.trees = removeDuplicateObj(trees, 'id')
            _this.queryRanks()
          } else Message.error(res.data.msg)
        })
      },
      queryRanks() {
        let _this = this
        let {getRanking} = Apis.member
        getRanking(_this.rankForm).then(res => {
          if (res.data.status == 200) {
            _this.rankList = res.data.data
          } else Message.error(res.data.msg)
        })
      },
      getMyTask() {
        let _this = this
        let {getMyTask} = Apis.member
        getMyTask().then(res => {
          if (res.data.status == 200) {
            _this.waitOrders = res.data.data
          } else Message.error(res.data.msg)
        })
      },
      getAssignTask() {
        let _this = this
        let {assignerTask} = Apis.member
        assignerTask().then(res => {
          if (res.data.status == 200) {
            _this.assignOrders = res.data.data
          } else Message.error(res.data.msg)
        })
      },
      handleTask(obj) {
        let _this = this
        let { id, assignerId } = obj
        _this.handleTaskForm.id = id
        _this.handleTaskForm.assignerId = assignerId
        _this.dialogHandleTaskVisible = true
      },
      saveTask(formRef) {
        let _this = this
        if (_this.handleTaskForm.status == '2' && _this.handleTaskForm.remark == '') {
          Message.warning('处理结果为无法处理时备注为必填')
          return false
        }
        let {handlerTask} = Apis.member
        handlerTask(_this.handleTaskForm).then(res => {
          if (res.data.status == 200) {
            _this.init()
            _this.$refs[formRef].resetFields()
            _this.dialogHandleTaskVisible = false
          } else Message.error(res.data.msg)
        })
      },
      cancelTask(formRef) {
        let _this = this
        _this.$refs[formRef].resetFields()
        _this.dialogHandleTaskVisible = false
      }
    },
  }
</script>
<style lang="less" scoped>
  .member-box{
    height: 100%;
    overflow-y: auto;
    box-sizing: border-box;
    .query-btn-group{
      text-align: right;
    }
    .customer-list{
      display: flex;
      justify-content: space-between;
      .customer-common{
        width: 300px;
        padding: 15px;
        box-sizing: border-box;
        height: 180px;
        font-size: 18px;
        color: #000;
        font-weight: bold;
        border: 1px solid #000000;
        box-shadow:
          2.8px 2.8px 2.2px rgba(0, 0, 0, 0.02),
          6.7px 6.7px 5.3px rgba(0, 0, 0, 0.028),
          12.5px 12.5px 10px rgba(0, 0, 0, 0.035),
          22.3px 22.3px 17.9px rgba(0, 0, 0, 0.042),
          41.8px 41.8px 33.4px rgba(0, 0, 0, 0.05),
          100px 100px 80px rgba(0, 0, 0, 0.07) ;
        margin: 35px 15px 35px 0;
        &:last-child{
          margin-right: 0;
        }
        .num{
          height: 75px;
          line-height: 45px;
          text-align: center;
          padding: 15px 0;
          box-sizing: border-box;
          border-bottom: 1px solid #000;
        }
        .title{
          height: 75px;
          line-height: 75px;
          text-align: center;
        }
      }
    }
    .ranks,.wait-order,.assign-order{
      width: 100%;
      height: 400px;
      overflow-y: auto;
      border: 1px solid #000;
      box-sizing: border-box;
      .head{
        width: 100%;
        min-height: 60px;
        border-bottom:1px solid #000000 ;
        display: flex;
        padding: 5px;
        box-sizing: border-box;
        align-items: center;
        .title{
          font-size: 22px;
          font-weight: bold;
          width: 100px;
        }
      }
      .rank-list{
        padding: 5px;
        box-sizing: border-box;
      }
    }
  }
</style>
<style scoped>
  .head-query >>> .el-form-item__content {
    width: 100px;
  }
  .head-query >>> .el-form-item {
    margin-bottom: 0;
  }
</style>
